import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  Switch,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import branchService from '../../../services/branchService';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles(() => ({
  root: {}
}));

function BranchCreateForm({ className, ...rest }) {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
  const { t } = useTranslation();

  return (
    <Formik
      initialValues={{
        code: '',
        name: '',
        store_name: '',
        email: '',
        telephone: '',
        fax: '',
        note: '',
        zipcode: '',
        state: '',
        city: '',
        county: '',
        address: '',
        latitude: 0,
        longitude: 0,
        remark: '',
        opening_times: '',
        is_independent: false
      }}
      validationSchema={Yup.object().shape({
        code: Yup.string()
          .max(255)
          .required(t('is required', { name: t('Code') })),
        name: Yup.string()
          .max(255)
          .required(t('is required', { name: t('Name') })),
        store_name: Yup.string()
          .max(255)
          .required(t('is required', { name: t('Store Name') })),
        email: Yup.string()
          .email(t('Must be a valid email'))
          .max(255),
        latitude: Yup.number(),
        longitude: Yup.number(),
        is_independent: Yup.bool()
      })}
      onSubmit={async (
        values,
        { resetForm, setErrors, setStatus, setSubmitting }
      ) => {
        try {
          await await branchService.create({
            ...values
          });
          setStatus({ success: true });
          setSubmitting(false);
          enqueueSnackbar(t('Created', { name: t('Branch') }), {
            variant: 'success'
          });
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.response.data.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values
      }) => (
        <form
          className={clsx(classes.root, className)}
          onSubmit={handleSubmit}
          {...rest}
        >
          <Card>
            <CardContent>
              {errors.submit && (
                <Box mb={3}>
                  <Alert severity="error">{errors.submit}</Alert>
                </Box>
              )}
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label={t('Name')}
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.code && errors.code)}
                    fullWidth
                    helperText={touched.code && errors.code}
                    label={t('Code')}
                    name="code"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.code}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.store_name && errors.store_name)}
                    fullWidth
                    helperText={touched.store_name && errors.store_name}
                    label={t('Store Name')}
                    name="store_name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.store_name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.telephone && errors.telephone)}
                    fullWidth
                    helperText={touched.telephone && errors.telephone}
                    label={t('Telephone')}
                    name="telephone"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.telephone}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.email && errors.email)}
                    fullWidth
                    helperText={touched.email && errors.email}
                    label={t('Email address')}
                    name="email"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.email}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.fax && errors.fax)}
                    fullWidth
                    helperText={touched.fax && errors.fax}
                    label={t('Fax')}
                    name="fax"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.fax}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.note && errors.note)}
                    fullWidth
                    helperText={touched.note && errors.note}
                    label={t('Note')}
                    name="note"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.note}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.zipcode && errors.zipcode)}
                    fullWidth
                    helperText={touched.zipcode && errors.zipcode}
                    label={t('Zipcode')}
                    name="zipcode"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.zipcode}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.state && errors.state)}
                    fullWidth
                    helperText={touched.state && errors.state}
                    label={t('State')}
                    name="state"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.state}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.city && errors.city)}
                    fullWidth
                    helperText={touched.city && errors.city}
                    label={t('City')}
                    name="city"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.city}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.county && errors.county)}
                    fullWidth
                    helperText={touched.county && errors.county}
                    label={t('County')}
                    name="county"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.county}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.address && errors.address)}
                    fullWidth
                    helperText={touched.address && errors.address}
                    label={t('Address')}
                    name="address"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.address}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.latitude && errors.latitude)}
                    fullWidth
                    helperText={touched.latitude && errors.latitude}
                    label={t('Latitude')}
                    name="latitude"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.latitude}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.longitude && errors.longitude)}
                    fullWidth
                    helperText={touched.longitude && errors.longitude}
                    label={t('Longitude')}
                    name="longitude"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.longitude}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.remark && errors.remark)}
                    fullWidth
                    helperText={touched.remark && errors.remark}
                    label={t('Remark')}
                    name="remark"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.remark}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(
                      touched.opening_times && errors.opening_times
                    )}
                    fullWidth
                    helperText={touched.opening_times && errors.opening_times}
                    label={t('Opening Times')}
                    name="opening_times"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.opening_times}
                    variant="outlined"
                  />
                </Grid>
                <Grid item />
                <Grid item md={6} xs={12}>
                  <Typography variant="h5" color="textPrimary">
                    {t('Is Independent')}
                  </Typography>
                  <Switch
                    checked={values.is_independent}
                    color="secondary"
                    edge="start"
                    name="is_independent"
                    onChange={handleChange}
                    value={values.is_independent}
                  />
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button
                  variant="contained"
                  color="secondary"
                  type="submit"
                  disabled={isSubmitting}
                >
                  {t('Submit')}
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
}

BranchCreateForm.propTypes = {
  className: PropTypes.string
};

export default BranchCreateForm;
